
<!-- 模块：设置节点 -->
<template>
  <div class="comBox">

    <div class="selectBox">
      <div class="searchName">设置节点&nbsp;&nbsp;&nbsp;</div>
      <div class="searchVal">
        <el-button type="primary" size="mini" plain @click="add">批量添加节点</el-button>
      </div>
    </div>
<!--
    <div class="selectBox">
      <div class="searchName"></div>
      <div class="searchVal" style="text-align: right;">基准值</div>
    </div> -->

    <div class="selectBox">
      <div class="searchName">开始值：</div>
      <el-select class="comSelect" v-model="startTime" filterable collapse-tags placeholder="请选择" size="mini" @change="selectChange('start_reference', $event)">
        <el-option class="comSelectOptions" v-for="(val, key) in startArr" :key="'start_' + key" :label="val.label" :value="val.value"></el-option>
      </el-select>
    </div>

    <div class="selectBox">
      <div class="searchName">结束值：</div>
      <el-select class="comSelect" v-model="endTime" filterable collapse-tags placeholder="请选择" size="mini" @change="selectChange('end_reference', $event)">
        <el-option class="comSelectOptions" v-for="(val, key) in endArr" :key="'end_' + key" :label="val.label" :value="val.value"></el-option>
      </el-select>
    </div>

    <div class="selectBox textBox">
      <div class="searchName">开始基准值：</div>
      <span>KSJZZ</span>
    </div>

    <div class="selectBox textBox">
      <div class="searchName">结束基准值：</div>
      <span>JSJZZ</span>
    </div>

  </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  data() {
    return {
      /* 页面布局 */
      startArr: [{ label: '客户下单日期', value: 'KHXDRQ' }, { label: '款式图下单时间', value: 'KSTXDSJ' }, { label: '每季度', value: 'MJD' }, { label: '项目创建日期', value: 'XMCJRQ' }],
      endArr: [{ label: '客户交期', value: 'KHJQ' }, { label: '工厂交期', value: 'GCJQ' }],
      /* 值 */
      startTime: '', //       select绑定：开始值
      endTime: '', //         select绑定：结束值
      start_reference: '', // 开始基准值
      end_reference: '' //    结束基准值
    }
  },
  watch: {
    /**
     * [赋值：非新增页面]
     */
    '$store.state.pageType'() {
      if (this.$store.state.pageType) {
        /* 赋值：基准值 */
        const { start_reference, end_reference } = this.$store.state.templateData || {}
        if (start_reference !== null) {
          this.startTime = start_reference.toUpperCase()
          this.start_reference = start_reference.toUpperCase()
        }
        if (end_reference !== null) {
          this.endTime = end_reference.toUpperCase()
          this.end_reference = end_reference.toUpperCase()
        }
        /* 赋值：节点列表 */
        this.add(false)
      }
    }
  },
  computed: {
    ...mapState(['nodeList'])
  },
  methods: {
    /**
     * [下拉框：变化]
     * @param {[String]} name 属性名
     * @param {[String]} val  选项文字
     */
    selectChange(name, val) {
      this[name] = val
      /** 保存数据 **/
      this.$store.commit('saveData', { name, obj: val })
    },

    // [{"node_id":"2c9f10b66779c50801677c1aea6b0282","node_name":"染色完成","node_code":"DH-RSWC1","node_ierarchy":null},{"node_id":"2c9f10b66e18114f016e3ad259d617cc","node_name":"计划提报","node_code":"DH-JHTB1","node_ierarchy":null},{"node_id":"2c9f10b66c3e484d016c4b52869567c8","node_name":"项目创建","node_code":"DH-XMCJ","node_ierarchy":null}]
    // [{"node_id":"2c9f10b66779c50801677c1a1121027b","node_name":"毛条到厂","node_code":"DH-MTDC","node_ierarchy":null},{"node_id":"8a8a8062638a6cab01638aaf51ee0034","node_name":"服装合同下达","node_code":"DH-FZHTXD","node_ierarchy":null},{"node_id":"2c9f10b66779c50801677c1aea760284","node_name":"大货完成","node_code":"DH-DHWC","node_ierarchy":null}]

    /**
     * [批量添加节点]
     * @param {[Boolean]} addPage 是否：新增页面
     */
    add(addPage = true) {
      // /* 本地 */
      // const that = this
      // const { nodeList } = that
      // let local = nodeList.length ? JSON.parse(localStorage.getItem('asd')) || [] : JSON.parse(localStorage.getItem('ganttTemplateChoiceNodeData')) || []
      // if (!addPage) {
      //   /* 非新增页面 */
      //   local = that.$store.state.templateData.ganttTemplateDetail || [] // 模板页面数据：节点列表
      // }
      // /* 转对象：原数据 */
      // const obj = {}
      // nodeList.forEach(function (item) {
      //   obj[item.node_id] = item
      // })
      // /* 转对象：新数据 && 去重 */
      // let i = nodeList.length
      // const arr = []
      // local.forEach(function (item) {
      //   if (!obj[item.node_id]) {
      //     const addObj = { key: i, badge: [], submit_type: '1', sys_clac_formula: '', min_section_value: '', max_section_value: '', verification_remark: '' }
      //     if (addPage) {
      //       /* 新增页面 */
      //       arr.push(Object.assign({}, item, addObj))
      //     } else {
      //       /* 非新增页面 */
      //       item.submit_type = String(item.submit_type)
      //       arr.push(Object.assign({}, addObj, item))
      //     }
      //     i++
      //   }
      // })
      // this.$store.commit('saveData', { name: 'addNode', obj: true })
      // that.$store.commit('pushData', { name: 'nodeList', obj: arr })

      /* 服务器 */
      const that = this
      if (!addPage) {
        /* ----- 非新增页面 ----- */
        const { nodeList } = that
        const local = that.$store.state.templateData.ganttTemplateDetail || [] // 模板页面数据：节点列表
        /* 转对象：原数据 */
        const obj = {}
        nodeList.forEach(function (item) {
          obj[item.node_id] = item
        })
        /* 转对象：新数据 && 去重 */
        let i = nodeList.length
        const arr = []
        local.forEach(function (item) {
          if (!obj[item.node_id]) {
            const addObj = { key: i, badge: [], submit_type: '1', sys_clac_formula: '', min_section_value: '', max_section_value: '', verification_remark: '' }
            item.submit_type = String(item.submit_type)
            arr.push(Object.assign({}, addObj, item))
            i++
          }
        })
        that.$store.commit('saveData', { name: 'addNode', obj: true })
        that.$store.commit('pushData', { name: 'nodeList', obj: arr })
      } else {
        /* ----- 新增页面 ----- */
        const host = window.location.origin + '/nova/'
        // eslint-disable-next-line
        win({
          url: host + 'pages/nodeinfo/nodeInfolist.jsp?htmlchoice=true',
          param: {},
          width: 1100,
          height: 550,
          title: '批量添加节点',
          onClose: function () {
            const { nodeList } = that
            const local = JSON.parse(localStorage.getItem('ganttTemplateChoiceNodeData'))
            /* 转对象：原数据 */
            const obj = {}
            nodeList.forEach(function (item) {
              obj[item.node_id] = item
            })
            /* 转对象：新数据 && 去重 */
            let i = nodeList.length
            const arr = []
            local.forEach(function (item) {
              if (!obj[item.node_id]) {
                const addObj = { key: i, badge: [], submit_type: '1', sys_clac_formula: '', min_section_value: '', max_section_value: '', verification_remark: '' }
                arr.push(Object.assign({}, item, addObj))
                i++
              }
            })
            that.$store.commit('saveData', { name: 'addNode', obj: true })
            that.$store.commit('pushData', { name: 'nodeList', obj: arr })
          }
        })
      }
    }
  }
}
</script>

<style scoped>
.comBox {
  margin-top: 10px;
  padding: 10px 0;
  background: #F2F6FC;
  display: flex;
  flex-wrap: wrap;
  flex: 1;
}
.selectBox { /* 单个组件块 */
  width: 280px;
  font-size: 12px;
  margin: 0 10px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.searchName { /* 单个 label */
  width: 80px;
  min-width: 80px;
  text-align: right;
}
.searchVal { /* 下拉框组件 */
  flex: 1;
}
.comSelect { /* 下拉框组件 */
  flex: 1;
}
.textBox { /* 基准值文字容器 */
  width: auto;
  color: #F56C6C;
  justify-content: flex-start;
}
</style>

<style>
.comSelectOptions { /* 单个选项 */
  height: 25px !important;
  font-size: 12px !important;
  line-height: 25px !important;
  padding: 0 10px !important;
}
</style>



